import { useState } from 'react';
import {
  Card,
  Button,
  Switch,
  Space,
  Typography,
  Alert,
  Spin,
  message,
  Empty,
  List,
  Tag,
  Tooltip,
} from 'antd';
import {
  ExclamationCircleOutlined,
  SearchOutlined,
  CheckCircleOutlined,
  CloseCircleOutlined,
  WarningOutlined,
  ToolOutlined,
} from '@ant-design/icons';
import { useApp } from '../contexts/AppContext';
import { ACTION_TYPES } from '../contexts/AppContext';
import apiService from '../services/apiService';

const { Title, Text, Paragraph } = Typography;

const ConflictsPage = () => {
  const { dispatch } = useApp();
  const [conflictsData, setConflictsData] = useState(null);
  const [autoResolve, setAutoResolve] = useState(false);
  const [loading, setLoading] = useState(false);

  const detectConflicts = async () => {
    setLoading(true);
    dispatch({
      type: ACTION_TYPES.SET_LOADING,
      payload: { key: 'conflicts', value: true },
    });

    try {
      const data = await apiService.detectConflicts(autoResolve);
      setConflictsData(data);
      
      dispatch({
        type: ACTION_TYPES.SET_CONFLICTS,
        payload: data,
      });
      
      const conflictCount = data.conflicts ? data.conflicts.length : 0;
      dispatch({
        type: ACTION_TYPES.SET_STATUS_MESSAGE,
        payload: `Conflict detection completed - ${conflictCount} conflicts found`,
      });
      
      if (conflictCount === 0) {
        message.success('No conflicts detected!');
      } else {
        message.warning(`Found ${conflictCount} conflicts`);
      }
    } catch (error) {
      message.error(`Failed to detect conflicts: ${error.message}`);
      console.error('Failed to detect conflicts:', error);
    } finally {
      setLoading(false);
      dispatch({
        type: ACTION_TYPES.SET_LOADING,
        payload: { key: 'conflicts', value: false },
      });
    }
  };

  const getConflictIcon = (type) => {
    switch (type?.toLowerCase()) {
      case 'security':
        return <CloseCircleOutlined style={{ color: '#ff4d4f' }} />;
      case 'configuration':
        return <WarningOutlined style={{ color: '#faad14' }} />;
      case 'dependency':
        return <ExclamationCircleOutlined style={{ color: '#1890ff' }} />;
      case 'resource':
        return <ToolOutlined style={{ color: '#722ed1' }} />;
      default:
        return <ExclamationCircleOutlined style={{ color: '#666' }} />;
    }
  };

  const getConflictSeverity = (severity) => {
    switch (severity?.toLowerCase()) {
      case 'high':
      case 'critical':
        return { color: 'red', text: 'High' };
      case 'medium':
        return { color: 'orange', text: 'Medium' };
      case 'low':
        return { color: 'yellow', text: 'Low' };
      default:
        return { color: 'default', text: 'Unknown' };
    }
  };

  const renderConflictCard = (conflict, index) => {
    const severity = getConflictSeverity(conflict.severity);
    
    return (
      <Card
        key={index}
        size="small"
        style={{ marginBottom: 16 }}
        title={
          <Space>
            {getConflictIcon(conflict.type)}
            <Text strong>{conflict.resource_id || conflict.resourceID}</Text>
            <Tag color={severity.color}>{severity.text}</Tag>
            {conflict.auto_resolved && (
              <Tag color="success" icon={<CheckCircleOutlined />}>
                Auto-Resolved
              </Tag>
            )}
          </Space>
        }
        extra={
          <Text type="secondary" style={{ fontSize: 12 }}>
            {conflict.type || 'Unknown Type'}
          </Text>
        }
      >
        <Space direction="vertical" size="middle" style={{ width: '100%' }}>
          <div>
            <Text strong>Conflict Details:</Text>
            <Paragraph style={{ marginBottom: 0, marginTop: 4 }}>
              {conflict.details || conflict.description || 'No details available'}
            </Paragraph>
          </div>

          {conflict.affected_resources && conflict.affected_resources.length > 0 && (
            <div>
              <Text strong>Affected Resources:</Text>
              <div style={{ marginTop: 4 }}>
                {conflict.affected_resources.map((resource, idx) => (
                  <Tag key={idx} style={{ margin: 2 }}>
                    {resource}
                  </Tag>
                ))}
              </div>
            </div>
          )}

          {conflict.resolution_steps && conflict.resolution_steps.length > 0 && (
            <div>
              <Text strong>Resolution Steps:</Text>
              <List
                size="small"
                dataSource={conflict.resolution_steps}
                renderItem={(step, idx) => (
                  <List.Item style={{ padding: '4px 0' }}>
                    <Text>{idx + 1}. {step}</Text>
                  </List.Item>
                )}
              />
            </div>
          )}

          {conflict.recommendations && (
            <div>
              <Text strong>Recommendations:</Text>
              <Paragraph style={{ marginBottom: 0, marginTop: 4 }}>
                {conflict.recommendations}
              </Paragraph>
            </div>
          )}

          {conflict.impact && (
            <Alert
              message="Impact Assessment"
              description={conflict.impact}
              type="info"
              showIcon
              size="small"
            />
          )}
        </Space>
      </Card>
    );
  };

  const renderConflictsSummary = () => {
    if (!conflictsData || !conflictsData.conflicts) return null;

    const conflicts = conflictsData.conflicts;
    const totalConflicts = conflicts.length;
    const resolvedConflicts = conflicts.filter(c => c.auto_resolved).length;
    const highSeverity = conflicts.filter(c => 
      c.severity?.toLowerCase() === 'high' || c.severity?.toLowerCase() === 'critical'
    ).length;
    const mediumSeverity = conflicts.filter(c => 
      c.severity?.toLowerCase() === 'medium'
    ).length;
    const lowSeverity = conflicts.filter(c => 
      c.severity?.toLowerCase() === 'low'
    ).length;

    return (
      <Card style={{ marginBottom: 16 }}>
        <div style={{ 
          display: 'grid', 
          gridTemplateColumns: 'repeat(auto-fit, minmax(120px, 1fr))', 
          gap: 16, 
          textAlign: 'center' 
        }}>
          <div>
            <Title level={3} style={{ margin: 0, color: totalConflicts > 0 ? '#ff4d4f' : '#52c41a' }}>
              {totalConflicts}
            </Title>
            <Text type="secondary">Total Conflicts</Text>
          </div>
          
          {resolvedConflicts > 0 && (
            <div>
              <Title level={3} style={{ margin: 0, color: '#52c41a' }}>
                {resolvedConflicts}
              </Title>
              <Text type="secondary">Auto-Resolved</Text>
            </div>
          )}
          
          {highSeverity > 0 && (
            <div>
              <Title level={3} style={{ margin: 0, color: '#ff4d4f' }}>
                {highSeverity}
              </Title>
              <Text type="secondary">High Severity</Text>
            </div>
          )}
          
          {mediumSeverity > 0 && (
            <div>
              <Title level={3} style={{ margin: 0, color: '#faad14' }}>
                {mediumSeverity}
              </Title>
              <Text type="secondary">Medium Severity</Text>
            </div>
          )}
          
          {lowSeverity > 0 && (
            <div>
              <Title level={3} style={{ margin: 0, color: '#52c41a' }}>
                {lowSeverity}
              </Title>
              <Text type="secondary">Low Severity</Text>
            </div>
          )}
        </div>
      </Card>
    );
  };

  const renderConflictsList = () => {
    if (!conflictsData) return null;

    if (!conflictsData.conflicts || conflictsData.conflicts.length === 0) {
      return (
        <Card>
          <Empty
            description="No conflicts detected"
            image={<CheckCircleOutlined style={{ fontSize: 64, color: '#52c41a' }} />}
          >
            <Text type="secondary">Your infrastructure appears to be conflict-free!</Text>
          </Empty>
        </Card>
      );
    }

    // Sort conflicts by severity (high -> medium -> low)
    const sortedConflicts = [...conflictsData.conflicts].sort((a, b) => {
      const severityOrder = { critical: 4, high: 3, medium: 2, low: 1 };
      const aSeverity = severityOrder[a.severity?.toLowerCase()] || 0;
      const bSeverity = severityOrder[b.severity?.toLowerCase()] || 0;
      return bSeverity - aSeverity;
    });

    return (
      <div>
        {sortedConflicts.map((conflict, index) => renderConflictCard(conflict, index))}
      </div>
    );
  };

  return (
    <div style={{ maxWidth: 1200, margin: '0 auto' }}>
      <div style={{ marginBottom: 24 }}>
        <Title level={2}>Resource Conflicts</Title>
        <Paragraph type="secondary">
          Detect and resolve conflicts in your infrastructure configuration
        </Paragraph>
      </div>

      <Card>
        <Space 
          size="large" 
          style={{ 
            width: '100%', 
            display: 'flex', 
            justifyContent: 'space-between',
            alignItems: 'center',
            flexWrap: 'wrap' 
          }}
        >
          <div style={{ display: 'flex', alignItems: 'center', gap: 8 }}>
            <Text strong>Auto-resolve conflicts where possible:</Text>
            <Tooltip title="Automatically resolve conflicts that have clear solutions">
              <Switch
                checked={autoResolve}
                onChange={setAutoResolve}
                size="small"
              />
            </Tooltip>
          </div>
          
          <Button
            type="primary"
            icon={<SearchOutlined />}
            onClick={detectConflicts}
            loading={loading}
            size="large"
          >
            Detect Conflicts
          </Button>
        </Space>
      </Card>

      {loading ? (
        <div style={{ textAlign: 'center', padding: '50px 0' }}>
          <Spin size="large" />
          <div style={{ marginTop: 16 }}>
            <Text>Analyzing infrastructure for conflicts...</Text>
          </div>
        </div>
      ) : conflictsData ? (
        <>
          {renderConflictsSummary()}
          {renderConflictsList()}
          
          {conflictsData.scan_time && (
            <Card size="small">
              <Text type="secondary">
                Scan completed in {conflictsData.scan_time}ms ‚Ä¢ 
                Last updated: {new Date().toLocaleString()}
              </Text>
            </Card>
          )}
        </>
      ) : (
        <Card style={{ marginTop: 16 }}>
          <Empty
            description="No conflict analysis performed yet"
            image={Empty.PRESENTED_IMAGE_SIMPLE}
          >
            <Button type="primary" onClick={detectConflicts}>
              Start Conflict Detection
            </Button>
          </Empty>
        </Card>
      )}
    </div>
  );
};

export default ConflictsPage;